
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700|Poppins:400,500&display=swap');
*{

    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
}
/*.bg-img{
    background: url('bg.jpg');
    height: 100vh;
    background-size: cover;
    background-position: center;
}*/
.bg-img:after{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.6);
}
.details{
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 999;
    text-align: center;
    padding: 60px 32px;
    width: 400px;
    transform: translate(-50%,-50%);
    background: rgba(255,255,255,0.05);
    box-shadow: -1px 4px 28px 0px rgba(0,0,0,0.75);
}
.form_title{
    color: white;
    font-size: 33px;
    font-weight: 600;
    margin: 0 0 35px 0;
    font-family: 'Montserrat',sans-serif;
}
.field{

    position: relative;
    height: 45px;
    width: 100%;
    display: flex;
    background: rgba(255,255,255,0.94);
}
.field span{
    font-size: 15px;
    color: #222;
    width: 40px;
    line-height: 45px;
}
.field input{

    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: #222;
    font-size: 16px;
    font-family: 'Poppins',sans-serif;
}
.a{
    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: #222;
    font-size: 16px;
    font-family: 'Poppins',sans-serif;

}
.space{
    margin-top: 16px;
}
.pass{
    text-align: left;
    margin: 10px 0;
}
.pass a{
    color: white;
    text-decoration: none;
    font-family: 'Poppins',sans-serif;
}
.pass:hover a{
    text-decoration: underline;
    color: #3498db;
}
.field input[type="text"]{
    color: white;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 600;
    font-family: 'Montserrat',sans-serif;
}
.field input[type="password"]{
    color: white;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 600;
    font-family: 'Montserrat',sans-serif;
}


.field input[type="submit"]{
    background: #3498db;
    border: 1px solid #2691d9;
    color: white;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 600;
    cursor: pointer;
    font-family: 'Montserrat',sans-serif;
}

.field input[type="submit"]:hover{
    background: #2691d9;
}

.login{
    color: white;
    margin: 20px 0;
    font-family: 'Poppins',sans-serif;
}
.links{
    display: flex;
    cursor: pointer;
    color: white;
    margin: 0 0 20px 0;
}

.links i{
    font-size: 17px;
}
i span{
    margin-left: 8px;
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 16px;
    font-family: 'Poppins',sans-serif;
}
.signup{
    font-size: 15px;
    color: white;
    font-family: 'Poppins',sans-serif;
}
.signup a{
    color: #3498db;
    text-decoration: none;
}
.signup a:hover{
    text-decoration: underline;
}
.login{
    color:white;
    margin: 20px 0;
    font-family: 'Poppins',sans-serif;
}
.link{
    display: flex;
    cursor: pointer;
    color: white;
}
.login-google{
    background: none;
    font-size: 20px;
}

.pass2{
    text-align: center;
    margin: 10px 0;
}
.pass2 a{
    color: white;
    text-decoration: none;
    font-family: 'Poppins',sans-serif;
}
.pass2:hover a{
    color: #3498db;
    text-decoration: underline;
}

.phone{
    height: 80%;
    width: 30%;
    color: #222;
    font-size: 16px;
    font-family: 'Poppins',sans-serif;
}

.area_code{
    margin-right: 280px;
    top: 500px;
}

